// Кнопки

.btn {
  display: block;
  vertical-align: middle;
  outline: 0 none;
  border: 0 none;
}


.btn-search {
  width: 35px;
  background-image: url(../images/search-icon.png);
  background-repeat: no-repeat;
  background-position: center;
  background-color: #fff;
  border-radius: 4px;
}


.btn-menu {
	width: 155px;
	padding-left: 0;
	padding-right: 0;
	color: #181818;
	background-color: #fff;
	font-family: $font-family-PTSan-bold;
	text-align: left;
	background-image: url(../images/arrow.png);
  background-repeat: no-repeat;
  background-position: center right 0px;
	font-size: 16px;
	text-transform: uppercase;
	transition: 0.5s;

  @include max-w(768px) {
    width: 126px;
    font-size: 13px;
  }

  @include max-w(767px) {
    width: 145px;
    padding-left: 16px;
    color: #fff;
    background-image: url(../images/btn-menu-mob.png);
    background-color: #3eade2;
  } 
}


.btn-menu:hover {
  color: #3baee4;
  background-image: url(../images/arrow-hover.png);

  @include max-w(767px) {
    color: #666666;
    background-color: #3eade2;
    background-image: url(../images/btn-menu-mob.png);
  } 
}


.btn-menu_marg-l {
	margin-left: -17px;
}


.btn-authorization {
  width: 80px;
  padding-left: 0;
	padding-right: 0;
  font-family: $font-family-PTSan-bold;
  font-size: 16px;
  color: #252a54;
  text-align: right;
  text-transform: uppercase;
	background-color: #fff;
	background-image: url(../images/user-icon.png);
  background-repeat: no-repeat;
  background-position: center left 0px;
  transition: 0.5s;

  @include max-w(768px) {
    width: 62px; 
    background-image: url(../images/user-icon-768.png);
    background-position: top -2px  left 0px;
    font-size: 13px;
  }
}


.btn-authorization:hover {
	color: #3baee4;
}


.button-menu {
  display: flex;
  align-items: center;
}




//slick btn

.slick-prev,
.slick-next {
	display: block;
	position: absolute;
	top: 100px;
	width: 37px;
	padding: 0;
  height: 128px;
  vertical-align: middle;
  outline: 0 none;
  border: 0 none;
  background-color: #d2a66b;
  opacity: 0.88;
	text-indent: -9999px;
	transition: 0.5s;
	z-index: 10;

  @include max-w(768px) {
    height: 66px; 
    width: 20px;
    top: 101px;
  }

  @include max-w(500px) {
    width: 20px;
    height: 28px;
    top: 85px;
  }

  @include max-w(375px) {
    top: 40px;
  }
}


.slick-prev {
	background-image: url(../images/slick-arrow-left.png);
  background-repeat: no-repeat;
  background-position: center center;

  @include max-w(768px) {
    background-image: url(../images/slick-arrow-left-768.png); 
  }
}


.slick-next {
	right: 0;
	background-image: url(../images/slick-arrow-right.png);
  background-repeat: no-repeat;
  background-position: center center;

  @include max-w(768px) {
    background-image: url(../images/slick-arrow-right-768.png); 
  }
}


.slick-prev:hover {
	background-color: #ffffff;
	background-image: url(../images/slick-arrow-left-hover.png);

  @include max-w(768px) {
    background-image: url(../images/slick-arrow-left-768-hover.png); 
  }
}


.slick-next:hover {
	background-color: #ffffff;
	background-image: url(../images/slick-arrow-right-hover.png);

  @include max-w(768px) {
    background-image: url(../images/slick-arrow-right-768-hover.png); 
  }
}



//slick-dots

.slick-dots {
	display: flex;
	justify-content: space-between;
	align-items: center;
  width: 98px;
  padding-top: 47px;
  padding-bottom: 32px;
  margin:  0 auto;
  
  @include max-w(768px) { 
    padding-top: 37px;
    display: none!important;
  }

   li {
   	width: 18px;
    height: 18px;
   	text-indent: -9999px;
   	background-color: #cb944c;
    opacity: 0.33;    
    border-radius: 13px;
    cursor: pointer;

   	button {
   		text-indent: -9999px;
      outline: 0 none;
      border: 0 none;
   	}
  }
}


.slick-active {
	opacity: 1!important;  
}



/*---------------------.burger-menu-------------------*/


.burger-menu {
  position: relative;
  width: 54px;
  height: 40px;
  margin-bottom: 13px;
  z-index: 100;

  &__container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 54px;
    height: 40px;
    background-color: #3eade2;
    cursor: pointer;
  }
}

.bar1, .bar2, .bar3 {
  width: 19px;
  height: 2px;
  background-color: #fff;
  margin: 2px;
  transition: 0.4s;
}

/* Rotate first bar */
.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-4px, 3px) ;
  transform: rotate(-45deg) translate(-4px, 4px) ;
}

/* Fade out the second bar */
.change .bar2 {
  opacity: 0;
}

/* Rotate last bar */
.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -7px) ;
  transform: rotate(45deg) translate(-5px, -5px) ;
}
