.page {
   padding-top: 30px;

   @include max-w(768px) {
      padding-top: 20px;
   }

   @include max-w(375px) {
      padding-top: 15px;
   }
}

.slider {
	position: relative;

	&__item {
      padding-top: 99px;
      padding-bottom: 99px;

      @include max-w(768px) {
      	padding-top: 100px;
         padding-bottom: 100px;	  
      }

      @include max-w(500px) {
         padding-top: 80px;
         padding-bottom: 80px
      }

      @include max-w(375px) {
         padding-top: 35px;
         padding-bottom: 35px
      }
	}

	&__content {
		width: 500px;
		margin-left: 70px;

		@include max-w(768px) {
      	width: 287px;
         margin-left: 37px;	  
      }

      @include max-w(500px) {
         width: 191px;
         margin-left: 27px;    
      }

		span {		
			display: block;
			padding-top: 17px;
			padding-bottom: 17px;
			padding-left: 35px;
         color: #ffffff;
         font-size: 43px;
         line-height: 47.66px;
			background-color: #d2a66b;
         opacity: 0.88;

         @include max-w(768px) {
         	padding-top: 10px;
			   padding-bottom: 10px;
			   padding-left: 18px;
            font-size: 23px;
            line-height: 23.12px;
         }

         @include max-w(500px) {
            padding-left: 8px;
            font-size: 15px;
            line-height: 17.78px;
         }
		}
	}
}


.slider__item-img-1,
.slider__item-img-2,
.slider__item-img-3 {
   background-image: url(../images/slider-img-1.jpg);
   background-size: cover;
   background-position: 50% 100%;

   @include max-w(500px) {
      background-image: url(../images/slider-img-mob-1.jpg);  
   }
}