// Все элементы формы


.search-header__field {
  display: block;
  width: 100%;
  padding-left: 8px;
  background-color: #fff;
  border: none;
  outline: none;
  border-radius: 4px;
}


//placeholder
::-webkit-input-placeholder {
	color: #b9b8bd;

	@include max-w(768px) {
    font-size: 10px;
  }
}

::-moz-placeholder {
	color: #b9b8bd;

	@include max-w(768px) {
    font-size: 10px;
  }
}/* Firefox 19+ */

:-moz-placeholder {
	color: #b9b8bd;

	@include max-w(768px) {
    font-size: 10px;
  }
}/* Firefox 18- */

:-ms-input-placeholder {
	color: #b9b8bd;

	@include max-w(768px) {
    font-size: 10px;
  }
}