.page-header {
  position: relative;
  padding-top: 29px;

   @include max-w(768px) {
      padding-top: 23px;
   }

   @include max-w(767px) {
     padding-top: 0;
   }
}


.header-container {
	max-width: 1240px;
	margin-left: 13px;
	margin-right: 13px;
	margin: 0 auto;

	@include max-w(1365px) {
      max-width: 1384px;
      padding-left: 15px;
      padding-right: 15px;
   }
}


.header {

   &__top {
   	display: flex;
      justify-content: space-between;
	   align-items: center;
	   padding-bottom: 10px;
      padding-right: 13px;
      padding-left: 13px;
      border-bottom: 1px solid #dddfdf;

      @include max-w(768px) {
        align-items: flex-start;
        padding-bottom: 2px;
        border-bottom: none;
      }

      @include max-w(767px) { 
        display: block;
      }
   }

	&__left {
		flex-basis: 788px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-shrink: 9;

      @include max-w(910px) {
         flex-basis: 440px;
      }
      
      @include max-w(768px) {
         align-items: flex-start;
      }

		.logo {
			max-width: 247px;

			@include max-w(1177px) {
           margin-right: 15px;
         }

         @include max-w(1023px) {
         	width: 179px;
         }

         @include max-w(767px) {
            width: 192px;
            margin-right: auto;
            margin-left: 0;
         }

			span {
            display: block;
            margin-top: 4px;
            color: #747688;

            @include max-w(1023px) {
         	   font-size: 12px;
            }

            @include max-w(767px) {
               font-size: 13px;
            }
			}
		}

		.form-header {
			width: 496px;

         @include max-w(1023px) {
            width: 60%;
         }

         @include max-w(910px) {
            width: 60%;
         }

         @include max-w(777px) {
            width: 221px;
         }
		}
	}

	&__right {
		flex-basis: 344px;
		display: flex;
      justify-content: space-between;
      align-items: center;
      flex-shrink: 1;

      @include max-w(1199px) {
        margin-left: 15px;
      }

      @include max-w(910px) {
         flex-basis: 300px;
      }

      @include max-w(768px) {
         flex-basis: 240px;
         align-items: flex-start;
         margin-left: 0;
      }

      @include max-w(767px) {
         display: none;
      }
	}
}


.form-header_flex {
	display: flex;
	justify-content: space-between;
}


.form-header {
	padding-top: 9px;
   padding-bottom: 9px;
	border: 1px solid #bab9be;
	border-radius: 4px;
	transition: 0.5s;

   @include max-w(768px) {
      padding-top: 5px;
      padding-bottom: 5px;
   }
}


.form-header_mob {

   @include max-w(767px) {
      display: none;
   }
}

.form-header:hover {
  border: 1px solid #3baee4;
}


.schedule {
	display: flex;
   flex-direction: column;


   span {
     font-size: 14px;

      @include max-w(910px) {
         font-size: 10px;
      }
   }

   span:nth-child(1) {
		color: #cb944c;
   }

   span:nth-child(2) {
		color: #2b3056;
   }
}

.header__tel {

   a {
		color: #181616;
		font-size: 23px;
		font-weight: 400;

      @include max-w(910px) {
         font-size: 15px;
      }
   }
}



/*-----------------.header__bottom------------------------*/



.header__bottom {
   display: flex;
   justify-content: space-between;
   padding-top: 17px;
   padding-right: 13px;
   padding-left: 13px;
   padding-bottom: 12px;
   border-bottom: 1px solid #dddfdf;

   @include max-w(1199px) {
      flex-direction: column;
   }

   @include max-w(768px) {
      padding-top: 0;
      padding-right: 0;
      padding-left: 0;
   }

   @include max-w(767px) { 
      position: absolute;
      top: -1px;
      left: -1px; 
   } 
}

.menu {
   position: relative;
   width: 820px;

   @include max-w(1199px) {
      order: 2;
      margin-top: 20px;
      width: 100%;
   }

   @include max-w(768px) {
      margin-top: 16px;
      padding-right: 13px;
      padding-left: 13px;
   }

   @include max-w(767px) {
      display: none;
      width: 400px;
      margin-top: 0;
      padding-top: 70px;
      padding-bottom: 20px;
      border-radius: 0 0 7px 0;
      background-color: #3eade2;
      z-index: 99;
   }

   @include max-w(414px) {
     width: 414px;
   }

   @include max-w(412px) {
     width: 412px;
   }

   @include max-w(375px) {
     width: 375px;
   }

   @include max-w(360px) {
     width: 360px;
   }

   @include max-w(320px) {
     width: 320px;
   }



   &__icon-list {
   	display: block;
   	width: 16px;
      height: 12px;
      margin-right: 23px;
      background-image: url(../images/list-icon.png);
      background-repeat: no-repeat;
      background-position: center right 0px;

      @include max-w(768px) {
        background-image: url(../images/list-icon-768.png);
      }

      @include max-w(767px) {
         display: none;
      } 
   }

	nav {

		ul {
         display: flex;
         justify-content: space-between;
         align-items: center;
         width: 100%;

         @include max-w(767px) {
            flex-direction: column;
            align-items: flex-start;
         }

			li {

            @include max-w(767px) {
              margin-bottom: 20px;
            }

				a {
					color: #181818;
					font-family: $font-family-PTSan-bold;
					font-size: 16px;
					transition: 0.5s;

               @include max-w(768px) {
                  font-size: 13px;
               }

               @include max-w(767px) {
                 color: #fff;
               } 
				}

				a:hover {
               color: #3baee4;

               @include max-w(767px) {
                  color: #666666;
               }
				}
			}
		}
	}
}



/*-------------------.authorization----------------------*/



.authorization {
	width: 320px;
	display: flex;
	justify-content: space-between;
	align-items: center;

	@include max-w(1199px) {
		margin-left: auto;
		margin-right: 0;
   }

   @include max-w(768px) {
      width: 100%;
      display: flex;
      justify-content: flex-end;
      padding-bottom: 8px;
      padding-right: 13px;
      border-bottom: 1px solid #dddfdf;
   }

   @include max-w(767px) {
     display: none;
   }

	&__basket {
		display: flex;
	   justify-content: space-between;
	   align-items: center;

	   .basket-quantity {
	   	display: block;
	   	width: 23px;
         height: 21px;
         padding-top: 2px;
         margin-right: 7px;
	   	background-color: #cc954b;
	   	color: #fff;
	   	font-family: $font-family-PTSan-bold;
	   	font-size: 14px;
	   	text-align: center;
	   	border-radius: 12px;

         @include max-w(768px) {
           width: 20px;
           height: 20px;
         } 
	   }

	   .basket-info {
	      color: #252654;
	      font-family: $font-family-PTSan-bold;
	      font-size: 14px;

         @include max-w(768px) {
           font-size: 10px;
         }
	   }

      .basket-info-num {
         font-size: 16px;

         @include max-w(768px) {
           font-size: 13px;
         }
      }
	}

   &__entry {

      @include max-w(768px) {
        margin-left: 17px;
      }
   }
}

.my-quantity {
	width: 23px;
   height: 23px;
   margin-right: 11px;
   background-image: url(../images/basket-icon.png);
   background-repeat: no-repeat;
   background-position: center left 0px;

   @include max-w(768px) {
      width: 15px;
      height: 15px;
      margin-right: 7px;
      background-image: url(../images/basket-icon-768.png);
   } 
}



/*--------------.pop-up-menu-----------------------*/



.pop-up-menu {
	display: none;
   position: absolute;
   width: 244px;
   padding-top: 26px;
   top: 17px;
   left: 0;
   z-index: 11;

   @include max-w(767px) {
      width: 215px;
      top: 64px;
   }

   ul {
      padding-top: 14px;
      padding-bottom: 13px;
      background-color: #fff;
      box-shadow: 2px 3px 1px rgba(108, 104, 104, 0.33);
      border: 1px solid #dddfdf;
      background-color: #ffffff;

   	li {
   		padding-left: 32px;
   		padding-right: 18px;
   		padding-top: 5px;
         padding-bottom: 5px;
   		transition: 0.5s;

   		a {
				color: #666666;
				font-size: 15px;
				line-height: 19px;
		   }		
   	}

   	li:hover {
         background-color: #e8e8ea;
   	}
   }
}


.marker {
	background-image: url(../images/marker-list.png);
   background-repeat: no-repeat;
   background-position: center left 15px; 
}


.marker-top {
	background-image: url(../images/marker-list.png);
   background-repeat: no-repeat;
	background-position: top 14px left 15px; 
}

.marker-arrow {
	background: url(../images/marker-arrow.png) center right 15px, 
	url(../images/marker-list.png) center left 15px;
   background-repeat: no-repeat;
}

.nested-list {
	position: relative;
   color: #666666;
   font-size: 15px;
   line-height: 19px;
   cursor: pointer;
}

.pop-up-nested {
	display: none;
	position: absolute;
	width: 244px;
	top: 0;
	right: -237px;
   padding-left: 10px;

   @include max-w(767px) {
      width: 215px;
      right: -208px
   }

   @include max-w(426px) {
      width: 210px;
      left: -3px;
      top: 28px
   }
	
	.pop-up-nested-title {
      color: #cb944c;
      padding-left: 15px;
      font-family: $font-family-PTSan-bold;
      font-size: 16px;
	   text-transform: uppercase;
	}
}




/*-------------------.mob-menu__list--------------------------*/


.mob-menu {
   display: none;

   @include max-w(767px) {
      display: flex;
   }
}

.mob-menu__list {
   width: 100%;
   background-color: #f0f1f6;
   margin-bottom: 13px;
   
   nav {

      ul {
         display: flex;
         flex-direction: row;
         justify-content: space-between;
         width: 100%;
         height: 40px;
         padding-left:  17px;
         padding-right: 17px;
         align-items: center;

         li {
            display: block;
            cursor: pointer;

            a {
               display: block;
            }
         }
      }
   }
}


.home-mob {
   width: 20px;
   height: 16px;
   background: url(../images/home-icon-mob.png) center;
   background-repeat: no-repeat;
}


.search-mob {
   width: 16px;
   height: 16px;
   background: url(../images/search-icon-mob.png) center;
   background-repeat: no-repeat;
}


.quantity-mob {
   width: 17px;
   height: 17px;
   background: url(../images/quantity-icon-mob.png) center;
   background-repeat: no-repeat;
}


.entry-mob {
   width: 19px;
   height: 16px;
   background: url(../images/entry-icon-mob.png) center;
   background-repeat: no-repeat;
}


.call-mob {
   width: 16px;
   height: 17px;
   background: url(../images/call-icon-mob.png) center;
   background-repeat: no-repeat;
}