//  Цвет

$body-bg: #fff;
$text-color: #181616;

// Цвет ссылки

$link-color: #55b3f3;
$link-color--hover: darken($link-color, 20%);

//  Типографика

$font-family-PTSan-regular: 'PTSans-Regular', Helvetica, Arial, sans-serif;
$font-family-PTSan-bold: 'PTSans-Bold', Helvetica, Arial, sans-serif;


$font-size: 14px;
$font-weight: 400; // Regular
$line-height: 1.2;

// Ширина контейнера

$container-padding: 15px;
$container-width: 1354px + $container-padding * 2;
